import LoginService from '../../services/login.service';

$(document).ready(function () {
  var $policy_form = $('#policy-form');

  var $email_form = $('#email-form');
  var $email_input = $('#email-input');
  var $send_email_btn = $('#send-email-btn');


  var $countdown = $('#countdown');
  var $codeInput = $('.code-input');
  var $certify_code_btn = $('#certify-code-btn');

  var $expirationText = $('.expiration-text');
  var email = null;
  const EMAIL_CERTIFY_STATE = {
    NONE:0, WAIT_INPUT_EMAIL:1, WAIT_INPUT_CODE:2, SUCCESS_CODE:3
  }
  var emailState = EMAIL_CERTIFY_STATE.NONE;

  var ProviderUser = {
    email: "",
    name: "",
    provider: "",
    nickname: ""
  };

  init();
  initView();
  initEvents();

  function init() {
    LoginService.getProviderUser()
      .then((res) => {
        //console.log("/account/temp/provideruser =====> :", res.data)
        ProviderUser.email = res.data.email;
        ProviderUser.name = res.data.name;
        ProviderUser.provider = res.data.provider;
        ProviderUser.nickname = res.data.nickname;
      }).catch((err) => {
        alert($.i18n.t("wrng_signup_retry"));
        //location.href = "/login";
      })
  }

  function initView() {
    $email_form.hide();
    $codeInput.hide();
    $expirationText.hide();
  }

  function initEvents() {
    $("#join_all_agree").click((e) => {
      if ($(e.target).prop("checked")) {
        $("input[type=checkbox]").prop("checked", true);
      } else {
        $("input[type=checkbox]").prop("checked", false);
      }
    });


    // 회원가입 버튼
    $("#join-btn").click(function (e) {
      e.preventDefault();
      if (!($("#join_user_agree").prop('checked') && $("#join_priv_agree").prop('checked'))) {
        alert(i18next.t('wrng_agree_terms'));
        return false;
      }
      if (ProviderUser.email != "") {
        // provider 로 부터 email을 받은 경우
        // 사용자 이메일 입력없이 정보동의만 받고 회원가입
        //certifyEmail(JoinEmail);
        alert(`[${ProviderUser.email}] ${$.i18n.t("sgst_verify_email")}`, () => {
          successJoin(ProviderUser.email, TOKEN);
        });
        return false;
      }

      if(emailState === EMAIL_CERTIFY_STATE.NONE) {
        $policy_form.hide();
        $email_form.show();
        emailState = EMAIL_CERTIFY_STATE.WAIT_INPUT_EMAIL;
      } else {
        alert($.i18n.t("wrng_complete_email"))
      }
    });

    // 메일이 없으면
    // 메일 주소 적고 이메일 인증 버튼 클릭
    $send_email_btn.click(function (e) {
      e.preventDefault();
      // email 폼 체크
      email = $.trim($email_input.val());
      if (!validateEmail(email)) {
        alert($.i18n.t("wrng_correct_email"));
        return false;
      }
      // enableEmailInput(false);
      certifyEmail(email);
    });

    // 코드 인증 버튼 클릭
    $certify_code_btn.click(function (e) {
      e.preventDefault();

      $countdown.countdown('pause');
      //console.log(`/api/user/joinauthcode?code=${$.trim($('#code').val())}&id=${email}`)
      let code = $.trim($('#code').val());

      LoginService.certifyCode(code, email, TOKEN)
        .then((res) => {
          if (res.status != 200) {
            alert(res.message);
            $countdown.countdown('resume');
            return false;
          }
          $countdown.countdown('stop');
          $codeInput.hide();

          // Object.assign(ProviderUser, {email})
          // emailState = EMAIL_CERTIFY_STATE.SUCCESS_CODE;
          successJoin(email, TOKEN)

        })
        .catch((err) => {
          console.log('error certifyEmail:', err);
        });
    });


  }

  const successJoin = (email, token) => {
    LoginService.createUser(email, token)
      .then((res) => {
        // console.log('user reg success :', res);
        if (res.status != 200) {
          alert(res.message);
          return false;
        } else {

          alert(res.message);
          location.href = "/";
        }
        //location.href = `/api/auth/${JoinProveder}`;
      })
      .catch((err) => {
        console.log('error certifyEmail:', err);
      });
  }


  function enableEmailInput(show) {
    if (show) {
      $send_email_btn.show();
      $email_input.attr("disabled", false);
    } else {
      $send_email_btn.hide();
      $email_input.attr("disabled", true);
    }
  }


  // 
  function certifyEmail(email) {
    var user = {
      "email": email,
      "name": ckeckName(),
    };
    LoginService.sendEmail(user)
      .then((res) => {
        //console.log('user reg success :', data);
        if (res.status != 200) {
          if (res.status === 422) {
            alert(i18next.t('wrng_already_email'))
          } else {
            alert(res.message);
          }
          enableEmailInput(true);
          return false;
        }
        $("#check-email-txt").show();

        $codeInput.show();
        emailState = EMAIL_CERTIFY_STATE.WAIT_INPUT_CODE;
        $countdown.countdown(new Date().getTime() + 180000, function (event) {
          $(this).text(event.strftime('%M:%S'));
        }).on('finish.countdown', function () {
          $codeInput.hide();
          $expirationText.show();
          enableEmailInput(true);
        });
      })
      .catch((err) => {
        console.log('error certifyEmail:', err);
      })

  } // End of certifyEmail

  function ckeckName() {
    if (ProviderUser.name == "") {
      if (ProviderUser.nickname == "") {
        ProviderUser.name = ProviderUser.email;
      } else {
        ProviderUser.name = ProviderUser.nickname;
      }
    }
    return ProviderUser.name;
  } // End of ckeckName

  function validateEmail(sEmail) {
    var filter = /^([\w-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/;
    if (filter.test(sEmail)) {
      return true;
    } else {
      return false;
    }
  } // End of validateEmail
});  // End of jQuery