import Fetch from '../utils/Fetch';
export default (() => {
  const getProviderUser = () => {
    //GET -> Provider User Info
    return Fetch({
      url: `/api/v1/user/oauth/provideruser?token=${TOKEN}`,
      type: 'GET',
    })
  }


  const sendEmail = (user) => {
    return Fetch({
      url: `/api/v1/user/join/email/certify?email=${user.email}&name=${user.name}`,
      type: 'GET',
      cache: false,
    });
  }


  // 이메일로 날아온 code 검증
  const certifyCode = (code, email, token) => {
    return Fetch({
      url: `/api/v1/user/join/email/authcode/certify?code=${$.trim($('#code').val())}&id=${email}&token=${token}`,
      type: 'GET',
      cache: false
    });
  }


  // 최종 완료
  const createUser = (email, token) => {
    return Fetch({
      url: '/api/v1/user',
      type: 'POST',
      contentType: "application/json",
      data: JSON.stringify({
        "token": token
      }),
      cache: false,

    });
  } // End of joinUser


  const logout = () => {
    return Fetch({
      url: "/api/v1/user/logout",
      type: 'GET',
      cache: false
    });
  }

  $.ajax

  return {
    getProviderUser,
    sendEmail,
    certifyCode,
    createUser,
    logout
  }
})()
